// Burger Menu
const headerBurger = document.querySelector('.header__burger');
const headerMenu = document.querySelector('.header__menu');
const headerLink = document.querySelectorAll('.header__link');
const md3 = window.matchMedia("(max-width: 767px)");
const body = document.querySelector('body')
// Is Iphone
const isiPhone = (navigator.userAgent.match(/iPhone/i) != null);
const isiPad = (navigator.userAgent.match(/iPad/i) != null);
const isiPod = (navigator.userAgent.match(/iPod/i) != null);

function burgerToggle() {
	headerBurger.classList.toggle('active');
	headerMenu.classList.toggle('active');
	body.classList.toggle('lock');
}

headerBurger.addEventListener('click', () => {
	burgerToggle()
})
// IOS Scroll Fix
let disableScroll = () => {
	let pagePosition = window.scrollY;
	document.querySelector('html').style.scrollBehavior = 'auto';
	document.body.classList.add('ios-lock');
	document.body.dataset.position = pagePosition;
	document.body.style.top = -pagePosition + 'px';
}

let enableScroll = () => {
	let pagePosition = parseInt(document.body.dataset.position, 10);
	document.body.style.top = 'auto';
	document.body.classList.remove('ios-lock');
	window.scroll({ top: pagePosition, left: 0 });
	document.querySelector('html').removeAttribute('style');
	document.body.removeAttribute('data-position');
}

let scrollLock_BtnListener = (item) => {
	item.addEventListener('click', (e) => {
		item.classList.toggle('scroll')
		if (item.classList.contains('scroll')) {
			disableScroll();
		} else {
			enableScroll();
		}
	});
}

if (isiPhone || isiPad || isiPod) {
	scrollLock_BtnListener(headerBurger);
}

if (md3.matches) {
	headerLink.forEach((el) => {
		el.addEventListener('click', () => {
			if (isiPhone || isiPad || isiPod) {
				enableScroll();
				headerBurger.classList.toggle('scroll');
			}
			setTimeout(() => {
				burgerToggle();
			}, 100)
		})
	})
}
// -- //
// Smooth scroll to link for IPhones
let smoothScroll = () => {
	let linkNav = document.querySelectorAll('[href^="#"]'),
		V = 0.2;  // скорость, может иметь дробное значение через точку (чем меньше значение - тем больше скорость)
	for (let i = 0; i < linkNav.length; i++) {
		linkNav[i].addEventListener('click', function (e) {
			e.preventDefault();
			let w = window.pageYOffset,
				hash = this.href.replace(/[^#]*(.*)/, '$1');  // !Убрать пробел у доллара и еденицы!!!
			t = document.querySelector(hash).getBoundingClientRect().top,
				start = null;
			requestAnimationFrame(step);
			function step(time) {
				if (start === null) start = time;
				let progress = time - start,
					r = (t < 0 ? Math.max(w - progress / V, w + t) : Math.min(w + progress / V, w + t));
				window.scrollTo(0, r);
				if (r != w + t) {
					requestAnimationFrame(step)
				} else {
					location.hash = hash
				}
			}
		}, false);
	}
}
if (isiPhone || isiPad || isiPod) {
	smoothScroll();
}

